.TodoInsert {
  display: flex;
  background: #495057;
}


.TodoInsert > input {
  background: none;
  outline: none;
  border: none;
  padding: 0.5rem;
  font-size: 1.125rem;
  line-height: 1.5;
  color: #fff;
  flex: 1;
}
.TodoInsert > input::placeholder {
  color: #dee2e6;
}


.TodoInsert > button {
  display: flex;
  align-items: center;
  cursor: pointer;
  background: none;
  outline: none;
  border: none;
  background: #868e96;
  color: #fff;
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 1.5rem;
  transition: 0.1s background ease-in;
}
.TodoInsert > button:hover {
  background: #adb5bd;
}
